import React, { useRef, useState } from 'react'
import "../style/style.css"
import {signup , useAuth , signin , logout} from './firebase'




const Login = () => {

  const [loading , setLoading] = useState(false);
  const currentUser = useAuth();
  const emailRef = useRef();
  const passwordRef = useRef();



  const handleSignup =  async () =>{
    setLoading(true)
    try{
      await signup(emailRef.current.value , passwordRef.current.value);
    }catch(e){
      console.log(e)
      alert("")
    }
    setLoading(false)
  }


  const handleLogin =  async () =>{
    setLoading(true)
    try{
      await signin(emailRef.current.value , passwordRef.current.value);
    }catch(e){
      alert("")
      console.log(e);
    }
    setLoading(false)
  }  

  const handleLogout =  async () =>{
    setLoading(true)
    try{
      await logout();
    }catch(e){
      alert("")
      console.log(e);
    }
    setLoading(false)
  }  
  return (
    <div className='loginAndSignUP'>

    
        <div className='loginDiv'>
        <p style = {{ fontSize: '30px' }}></p>
      
           :
           <br />
           <input  type="text" ref={emailRef} className='loginInput'placeholder='Email'/>
           <br />
           : 
           <br />
           <input type="password" ref={passwordRef} className='loginInput' placeholder='Password'/>
           <br />
            <button onClick={handleLogin} disabled={loading || currentUser !== null } ></button>
            <button onClick={handleLogout}  disabled={loading || currentUser == null} ></button>
            <button onClick={handleSignup} disabled={loading || currentUser !== null } ></button>
            <p>:{currentUser?.email}</p>
        </div>
        
    </div>
  )
}


export default Login